* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    line-height: 3;
  }
  
  nav ul {
    list-style-type: none;
    padding: 0;
  }
  
  nav ul li {
    display: inline;
    margin-right: 10px;
  }
  
  main {
    padding: 20px;
  }

  a {
  color: #007bff;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

  
  footer {
    text-align: center;
    padding: 10px;
    background-color: #f1f1f1;
  }


